.order-id-details {
  line-height: 18px;
  position: relative;
  .order-type {
    background: #3d2277;
    margin: 5px 0px;
  }
  .order-id {
    // padding-top: 3px;
  }
  .whatsapp-widget {
    // width: 15px;
    position: absolute;
    top: 0;
    right: 0;
  }
  .cart-id {
    margin-bottom: 3px;
  }
  .payment-track-update {
    display: flex;
    flex-direction: column;
    // margin-top: 2px;
    .payment-track {
      margin-bottom: 3px;
    }
    .button {
      padding: 5px;
      width: 80px;
      font-size: 10px;
      margin-top: 7px;
    }
  }
  .request-flag {
    background: red;
    color: #fff;
    padding: 1px 5px;
    font-size: 10px;
    text-align: center;
    transform: rotate(-45deg);
    position: absolute;
    z-index: 10;
    left: 4px;
    margin-top: 10px;
    // top: 170px;
    border-radius: 3px;
  }
  .custom-padding {
    padding: 1px 6px;
  }
  .addon-color {
    background-color: #3d2277;
  }
  // .custom-badge .custom-padding {
  //   padding: 2px 6px;
  // }
  .payment-detailes-header {
    font-size: 12px;
    line-height: 16px !important;
    .payment-type {
      font-size: 95%;
    }
    .payment-track-update {
      display: flex;
      flex-direction: column;
      margin-bottom: 6px;
      margin-top: 10px;
      .input-field {
        width: 200px;
        margin-top: 2px;
        padding: 0px 10px;
        height: 31px !important;
      }
      .button {
        padding: 5px;
        width: 80px;
        font-size: 10px;
        margin-top: 7px;
      }
    }
  }
  .cet-padding {
    padding-right: 8px;
  }
  .sale-benefit {
    margin-top: 5px !important;
    .custom-badge {
      padding: 1px 10px;
    }
  }
  .return-comment{
    .custom-badge{
      padding: 1px 7px;
    }
  }
}
.cancel-request {
  border-top: 1px solid;
  border-bottom: 1px solid;
  padding: 5px 0px;
  margin: 5px 0px;
  display: flex;
  flex-direction: column;
  .cancel-request-btn {
    background: red;
    padding: 3px 10px;
    border-radius: 15px;
    width: 120px;
    color: white;
    font-weight: 600;
  }
}
